@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base{
  @font-face{
    font-family: 'Inter';
    src: url('../assets/fonts/Inter-Regular.woff2') format('woff2');
    font-weight: 400;
    font-display: swap;
  }
  
  @font-face{
    font-family: 'Inter';
    src: url('../assets/fonts/Inter-Medium.woff2') format('woff2');
    font-weight: 500;
    font-display: swap;
  }
  
  @font-face{
    font-family: 'Inter';
    src: url('../assets/fonts/Inter-SemiBold.woff2') format('woff2');
    font-weight: 600;
    font-display: swap;
  }
  
  @font-face{
    font-family: 'Inter';
    src: url('../assets/fonts/Inter-ExtraBold.woff2') format('woff2');
    font-weight: 800;
    font-display: swap;
  }

  html{
    font-family: 'Inter', system-ui, sans-serif;
    color: #292D32;
  }
}


@layer components{
  .container{
    @apply p-5;
  }


  @media screen(lg) {
    .container{
      @apply p-10
    }
  }
}